	module.exports = {"data":{"content":{"content":"<p>本來你可以直接改寫原本的數組，比如利用這個方法：<code>arr.push(item)</code>。\n但在Redux裡面不可以這樣寫，因為會改寫原本數組，違反了immutable的原則。\n因此，要這樣寫： <code>arr: [...state.arr, action.newItem]</code>。</p>\n<p>例子：</p>\n<pre><code><pre><code class=\"hljs javascript\"><span class=\"hljs-keyword\">import</span> { ADD_ITEM } <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'../Actions/UserActions'</span>\n<span class=\"hljs-keyword\">const</span> initialUserState = {\n  <span class=\"hljs-attr\">arr</span>: []\n}\n\n<span class=\"hljs-keyword\">export</span> <span class=\"hljs-keyword\">default</span> <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> <span class=\"hljs-title\">userState</span>(<span class=\"hljs-params\">state = initialUserState, action</span>) </span>{\n  <span class=\"hljs-built_in\">console</span>.log(arr);\n  <span class=\"hljs-keyword\">switch</span> (action.type) {\n    <span class=\"hljs-keyword\">case</span> ADD_ITEM:\n      <span class=\"hljs-keyword\">return</span> {\n        ...state,\n        <span class=\"hljs-attr\">arr</span>: [...state.arr, action.newItem]\n      }\n\n    <span class=\"hljs-keyword\">default</span>:\n      <span class=\"hljs-keyword\">return</span> state;\n  }\n}\n</code></pre></code></pre>\n<p>另外，你也可以利用<code>concat()</code>方法。</p>\n<h2 id=參考資料>參考資料</h2>\n<ol>\n<li><a href=\"http://stackoverflow.com/questions/40911194/how-do-i-add-an-element-to-array-in-reducer-of-react-native-redux\">How do I add an element to array in reducer of React native redux? - stackoverflow</a></li>\n</ol>\n","title":"Redux push()是怎樣寫的？","createdDate":"2017-05-03T23:20+08:00","headerImgur":"IYhTVXU.png","id":"2dknwjF7t6s6iuugoM6CwM","toc":["參考資料"],"tags":"JavaScript, Redux"},"latestPosts":{"totalCount":60,"edges":[{"node":{"title":"【網易考拉校招】部門總監面對面-直播答疑邀約（附網易內推碼B222E54）","url":"netease-kaola-live","createdDate":"2018-08-01T20:00+08:00"}},{"node":{"title":"Uncaught ReferenceError: regeneratorRuntime is not defined 解決方法","url":"regenerator-runtime-is-not-defined","createdDate":"2018-07-29T20:00+08:00"}},{"node":{"title":"關於作者","url":"about-calpa-liu","createdDate":"2018-05-01T20:00+08:00"}},{"node":{"title":"【方法】在直接複製貼上內容的時候，可能已經不知不覺注入了一段不可視的「空白」","url":"insert-zero-width-characters","createdDate":"2018-04-06T23:00+08:00"}},{"node":{"title":"知乎無限 live 數據流網站","url":"zhihu-unlimited-lives-website","createdDate":"2018-03-28T20:49+08:00"}},{"node":{"title":"Gatsbyjs async/await 更新報錯解決方法","url":"gatsybjs-async-await-upgrade-bug-solution","createdDate":"2018-03-21T20:00+08:00"}}]}},"pathContext":{"id":"2dknwjF7t6s6iuugoM6CwM"}}

